<template>
  <!-- Modal -->
  <button
    type="button"
    class="btn btn-primary"
    data-bs-toggle="modal"
    data-bs-target="#B_MenuPlus"
  >
    메뉴추가하기
  </button>
  <div
    class="modal fade"
    id="B_MenuPlus"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="exampleModalLabel">메뉴추가하기</h1>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <B_MenuPluse />
          <!-- 이미지 업로드 input -->
          <input
            type="file"
            ref="fileInput"
            @change="handleFileUpload"
            accept="image/*"
          />
          <img :src="imageUrl" alt="Uploaded Image" v-if="imageUrl" />
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
          >
            Close
          </button>
          <button type="button" class="btn btn-primary" @click="saveChanges">
            Save changes
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import B_MenuPluse from "@/components/business/MS_B_MenuPluse.vue";
import axios from "axios";

export default {
  name: "B_MenuPlusMD",
  components: {
    B_MenuPluse,
  },
  props: {
    bnum: Number,
  },
  data() {
    return {
      imageUrl: null,
      formData: {
        fname: "", // 메뉴 이름
        finfo: "", // 메뉴 설명
        fprice: "", // 메뉴 가격
      },
    };
  },
  methods: {
    handleFileUpload(event) {
      const file = event.target.files[0];
      if (!file) return;

      // 이미지를 미리보기에 표시
      const reader = new FileReader();
      reader.onload = (e) => {
        this.imageUrl = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    saveChanges() {
      const formData = new FormData();
      //formData.append("fimg", this.$refs.fileInput.files[0]);

      // 메뉴 이름, 설명, 가격을 FormData에 추가
      formData.append("fname", this.formData.fname);
      formData.append("finfo", this.formData.finfo);
      formData.append("fprice", this.formData.fprice);

      console.log("formData:", formData);

      axios
        .post("/MenuInsert", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          console.log("메뉴 추가 성공:", response.data);
          // 성공 시 모달 닫기 등 필요한 동작 수행
        })
        .catch((error) => {
          console.error("메뉴 추가 실패:", error);
          // 실패 시 에러 처리
        });
    },
  },
};
</script>

