<template>
  <div class="container mt-5" style="height: 800px; overflow-y: auto">
    <div class="row justify-content-center">
      <div class="col-md-7 col-lg-8">
        <h4 class="mb-3">가게정보 수정</h4>
        <h2>{{ shop.sname }}</h2>
        <form class="needs-validation" @submit.prevent="submitForm">
          <div class="row g-3">
            <div class="col-sm-6">
              <label for="ShopName" class="form-label"
                >상호명: {{ shopName }}</label
              >
              <input
                type="text"
                class="form-control"
                id="ShopName"
                v-model="shopName"
                readonly
                disabled
              />
              <div class="invalid-feedback">Valid first name is required.</div>
            </div>
            <div class="col-sm-6">
              <label for="category" class="form-label"
                >분류{{ category }}</label
              >
              <input
                type="text"
                class="form-control"
                id="category"
                v-model="category"
                readonly
                disabled
              />
              <div class="invalid-feedback">Valid last name is required.</div>
            </div>

            <div class="col-12">
              <label for="time" class="form-label">운영시간</label>
              <!-- TimePicker 컴포넌트 사용 예시 -->
              <!-- <TimePicker :hour="selectedHour" :minute="selectedMinute" /> -->
              <input
                type="text"
                class="form-control"
                id="start"
                placeholder="운영시작시간"
                v-model="start"
                required
              />
              <input
                type="text"
                class="form-control"
                id="end"
                placeholder="운영종료시간"
                v-model="end"
                required
              />
              <div class="invalid-feedback">시간을 입력해주세요.</div>
            </div>

            <div class="form-group col-md-5">
              <label for="my_number">전화번호</label>
              <input
                type="tel"
                class="form-control"
                name="my_number"
                id="my_pnumber"
                maxlength="13"
                placeholder="전화번호"
                v-model="phoneNumber"
                readonly
                disabled
              />
            </div>

            <div class="col-12">
              <label for="address" class="form-label">주소</label>
              <input
                type="text"
                class="form-control"
                id="address"
                v-model="address"
                readonly
                disabled
              />
              <div class="invalid-feedback">
                Please enter your shipping address.
              </div>
            </div>
            <B_MenuPlusMD :bnum="bnum" />

            <button type="submit" class="btn btn-primary">저장</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import B_MenuPlusMD from "@/components/business/Ms_B_MenuPlusMD.vue";
import axios from "axios";

export default {
  name: "B_ShopRevise",
  components: {
    B_MenuPlusMD,
  },
  data() {
    return {
      shop: [], // 가게 정보를 담는 객체

      category: "", // 분류 데이터
      start: "",
      end: "",
      bnum: 1003, // 가게 번호
    };
  },
  watch: {
    start(val) {
      if (val.length === 4) {
        this.start = val.slice(0, 2) + ":" + val.slice(2);
      }
    },
    end(val) {
      if (val.length === 4) {
        this.end = val.slice(0, 2) + ":" + val.slice(2);
      }
    },
  },
  created() {
    this.fetchStoreInfo(this.bnum);
  },
  methods: {
    fetchStoreInfo(bnum) {
      axios
        .get(`/B_ShopDetail?bnum=${bnum}`)
        .then((response) => {
          this.shop = response.data;
          console.log("가게 정보 데이터:", response.data);
          this.shopName = this.shop.sname;
          this.category = this.shop.fcategory_mikey;
          this.phoneNumber = this.shop.stel;
          this.address = this.shop.saddr;
          this.start = this.shop.start_time;
          this.end = this.shop.end_time;
          this.name = this.shop.mname;
        })
        .catch((error) => {
          console.error("가게 정보 가져오기 실패:", error);
        });
    },
    submitForm() {
      const formData = {
        bnum: this.bnum, // 가게 번호 혹은 식별자 (필요 시 수정)
        start_time: this.start, // 시작 시간
        end_time: this.end, // 종료 시간
      };

      axios
        .post("/B_ShopTimeUpdate", formData)
        .then((response) => {
          console.log("서버 응답:", response.data);
        })
        .catch((error) => {
          console.error("서버 요청 실패:", error);
        });
    },
  },
};
</script>
